export default{
	name:"TabBar",
	props:{
		tmpData:{
			type:[Object,String]
		}
	},
	data(){
		return{
			top:24
		}
	},
	computed:{
		editable(){
			return this.$store.state.table.editable
		},
		hasRecords(){	
			if(this.$store.state.table.recordsAll && this.$store.state.table.recordsAll.length>0){
				return true;
			}		
		}
	},
	methods: {
		getActions(action){
			if(action === 'edit'){			
				// this.$store.commit('changeEditable',true)	
				// console.log(this.editable)
			}else if(action === 'create'){
				// this.$store.commit('changeEditable',true)	
				// this.editable = true;	
			}else if(action === 'save'){
				// this.$store.commit('changeEditable',false)	
				// this.editable = false;
			}else if(action === 'screen'){
				// this.editable = false;
				this.$store.commit("changeEditable",false)
			}
		   this.$emit('actFun',action)
		},
		tipFun(){
			uni.showToast({
				title:'请完成当前操作',
				icon:'none'
			})
		},
		topBarClick(){
			this.$store.commit("initActiveItem");
		}
	},
	mounted() {
		let system = uni.getSystemInfoSync();
		this.top = system.statusBarHeight;
	}
}
